@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
html{
  height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  height: 100%;
  width: 100%;
}

html {
  scroll-behavior: smooth;
}

.pos {
  align-self: start;
}

body {
  background-color: rgb(255, 255, 255);
  color:rgb(0, 0, 0);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.home {
  min-height: 60vh;
}

.announcement-box {
  min-height: 50vh;
}

#message-box {
  min-height: 40px;
  border: 1px grey;
}

/*----------------------------------------------------------------------------------------*/

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(29, 93, 182);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(29, 93, 182);
  border-radius: 10px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif;
}

p{
  font-family: 'Quicksand', sans-serif;
}

.meta-nav {
    min-height: 40px;
    background: white;
    color: #01579b;
    padding: 0px;
    margin: 0px;
    width: 100%;
}


.meta-ieee-logo {
      margin: -2px 0px 0px 0px;
    float:right;
}

@media (max-width: 768) {
.meta-nav {
    min-height: 100px;
    background: white;
    color: #01579b;
    padding: 0px;
    margin: 0px;
    width: 100%;
}
.meta-ieee-logo {
      padding: 20px 0px 0px 0px;
    float:right;
}
}


#ieee-meta-a {
    font-family: inherit;
    font-size: .9em;
    line-height: 125%;
    border: 0;
    margin: 0;
    color: #01579b;
    text-align: left;
    padding: 11px 36px;
}

#ieee-meta-a a:link, #ieee-meta-a a:visited, #ieee-meta-a a:active {
    color: #01579b;
    text-decoration: none;
}

#ieee-meta-a a:hover {
    color: #01579b;
    text-decoration: underline;

}

.toolbar_a{

    position: fixed;
    width: 100%;
    left: 0;
    top: 40px;
    background: white;
    height: 20px;
    z-index:5;
    padding: 2rem;
    padding-bottom: 2.5rem;
    outline: 0;



}
.toolbar_navigation{
    background-color: white;
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    box-shadow: none;
    outline: 0;

}

.drawer_toggle{
    align-items: center;
    margin-top: 0.5rem;
    outline: 0;
}
.toolbar_logo a{
    color: #01579b;
    text-decoration: none;
    font-size: 2rem;
    margin-left: 0.5rem;
    outline: 0;

}
.hi{
    flex:1 1;
}
.toolbar_navigation_items ul{
    color: #01579b;
    text-decoration: none;
    list-style: none;
    font-size: 1.25rem;
    display: flex;
    position: relative;
    float: right;
    outline: 0;


}
.toolbar_navigation_items li{
    padding: 0 0.5rem;
    outline: 0;

}
.toolbar_navigation_items a{
    color: #01579b;
    text-decoration: none;
    border-radius: 37px;
    outline: 0;
    padding-left: 30px;
    padding-right: 30px;
   
    
}


.toolbar_navigation_items a:hover{
    color: white;
    text-decoration: none;
    border-radius: 37px;
    background: #01579b;
    outline: 0;


}



@media(max-width: 900px){
    .toolbar_navigation_items{
        display: none;
        position: fixed;
    }

    .toolbar_a{
        position: fixed;
        width: 100%;
        left: 0;
        top: 40px;
        background: white;
        height: 56px;
        z-index:5;
        padding: 2px;




    }
}

.toggle_button{
    border: none;
    background-color: white;
    align-items: center;
    background: transparent;
    cursor: pointer;

}
.toggle_button:focus{
    outline: none;
}
@media(min-width:900px){
    .toggle_button{
        display: none;
    }
}

.restrict_margin{
    width:100%;
    height:auto;
    margin:auto;
}

#bottom_margin{
    margin-bottom:1.5em;
}

#gallery{
    z-index: 0;
}

.link{
  text-decoration: none;
  color: #01579b;
}
.link:hover{
  text-decoration: underline;
  color:  #777777;
}

.App{
    height: 100%;
    width: 100%;
}
#first{
    height: 100%;
    width: 100%;
}
.side-drawer{
    height: 100vh;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5) ;
    position: fixed;
    display: flex;
    flex-direction: column;
    top:0;
    right: 0;
    width: 40%;
    max-width: 400px;
    z-index: 200;
    overflow-y: auto;
}
 
.side-drawer a{
    color: #01579b;
    z-index: 200;
    text-decoration: none;
    width: 100%;
}
.side-drawer a:hover,
.side-drawer a:active{
    color: white;
    background: #01579b;
}
.side-drawer ul{
    color: #01579b;
    z-index: 200;
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    
}
.closer{
    border: none;
    background-color: #01579b;
    align-items: left;
    background: transparent;
    cursor: pointer;
    float: left;
}


  
  .main_img{
    width:100%;
    height:100%;
    margin:0;
    padding:0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
  }
  
  .iedc_img{
    margin-top:5em;
    background-repeat:no-repeat;
    height:100%;
    width:100%;
  }
  
  #about_img {
    width: 100%;
    height: 100%;
    border-radius: 20px;
  }
  
  .card_design{
    height: 50%;
  }
  
  .announcements_header{
    margin-top:1.5em;
  }
  
  #bottom_margin {
    margin-bottom: 1em;
  }
  
  #top_margin {
    margin-top: 2em;
    bottom: 0;
  }
  
  #box_curve_bottom {
    background-color:rgb(19, 8, 49); /*rgb(159, 85, 85)*/
  }
  
  #box_curve_top {
    background-color: rgb(199, 101, 101);
    padding-top: 0px;
    margin-bottom: 0;
  }

  #announcements_card {
    width: 100%;
    border-radius: 20px;
  }
  
  #center_align {
    text-align: center;
  }
  
  @media only screen and (max-width: 600px) {
    .announcement_header{
      font-size:20px;
      margin-top:30px;
    }
    .message_box{
      margin-top:0%;
    }
    #box_curve_bottom {
      border-bottom-left-radius: 10%;
      border-bottom-right-radius: 10%;
    }
  
    #box_curve_top {
      border-top-left-radius: 0%;
      border-top-right-radius: 15%;
      padding-top: 40px;
    }
    #centre_mobile {
      text-align: left;
    }
    .mobile_space {
      margin-bottom: 0px;
    }
    .margin_top_mobile {
      margin-top: 0em;
    }
  
    .pos {
      align-self: start;
    }
  
  }
  
.top{
  padding-top: 10px;
  padding-left: 0px;
}
.inform{
  padding-top: 0%;
  color: #777777
}
@media(min-width:768px){
    .top{
        padding-top: 79px;
        padding-left: 90px;
    }
}
@media(min-width:768px){
    .inform{
        padding-top: 19%;
    }
}

.head:hover{
  color: #01579b
}
.head{
  transition: all 0.3s linear
}

